angular.module('dotDirective', [])
	.directive("dot", function() {
  return {
    restrict: "E",
    scope: {
        methylation: '=',
        samples: '='
    },
    templateUrl: 'partials/methylationDot.html',
    replace: true,
    controller: [ '$scope', function ($scope) {
        var getRadius = function(samples){
            if (samples > 100){
                samples = 100
            } else if (samples < 20){
                samples = 20
            }
            return samples/10
        }
        $scope.radius = getRadius($scope.samples);
    }]
  }
});